import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentField = () =>
  request({
    prefix,
    url: `/Student/GetStudentField`,
    method: 'GET',
    params: {
    },
    data: {},
  });

export const getStudentWithFields = ({
  studyFieldId,
  trainTypeId,
  facultyId,
  courseId,
  classId,
  semesterId,
  yearEduId,
  fieldList
}) =>
  request({
    prefix,
    url: `/Student/GetStudentWithFields`,
    method: 'POST',
    data: {
      studyFieldId,
      trainTypeId,
      facultyId,
      courseId,
      classId,
      semesterId,
      yearEduId,
      fieldList
    },
    params: {},
  });

export const exportStudentWithFields = ({
    studyFieldId,
    trainTypeId,
    facultyId,
    courseId,
    classId,
    semesterId,
    yearEduId,
    fieldList
  }) =>
    requestDownload({
      defaultFileName: 'File_ExportStudentsByFields.xlsx',
      prefix,
      url: `/Student/ExportStudentWithFields`,
      method: 'POST',
      data: {
        studyFieldId,
        trainTypeId,
        facultyId,
        courseId,
        classId,
        semesterId,
        yearEduId,
        fieldList
      },
      params: {},
    });


